<template>
  <v-app id="sandbox">
 
    <v-navigation-drawer
      v-model="primaryDrawer.model"
      :clipped="primaryDrawer.clipped"
      :floating="primaryDrawer.floating"
      :mini-variant="primaryDrawer.mini"
      :permanent="primaryDrawer.type === 'permanent'"
      :temporary="primaryDrawer.type === 'temporary'"
      app
      overflow
      v-if="user && user.rights"
    >
    
     <v-list>
       <template v-for="(item, index) in drawerList"   >
        <v-list-item    :key="index" :to="item.path">
          <v-list-item-icon>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title>{{ item.name }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
       </template>
      </v-list>
    
    </v-navigation-drawer>

    <v-app-bar
      :clipped-left="primaryDrawer.clipped"
      app
    >
      <v-app-bar-nav-icon
        v-if="primaryDrawer.type !== 'permanent'"
        @click.stop="primaryDrawer.model = !primaryDrawer.model"
      ></v-app-bar-nav-icon>
      <v-toolbar-title>Roaa</v-toolbar-title>
      <v-spacer></v-spacer>
      <!-- <v-icon> mdi-bell </v-icon> -->
      <v-menu v-if="user"
        v-model="menu"
        :close-on-content-click="false"
      
      >
        <template v-slot:activator="{ on }">
        
          <v-list dense color="transparent" class="ma-0">
            <v-list-item v-on="on">
             <v-list-item-avatar class="ml-1">
                  <v-avatar  color="red" v-on="on" size="35">
                    <span class="white--text ">{{displayName.substring(0,2).toUpperCase()}}</span>
                  </v-avatar>
              </v-list-item-avatar>
              <v-list-item-content>
                <v-list-item-title>
                  {{ displayName }}
                </v-list-item-title>
                <v-list-item-subtitle>
                  {{user.role}}
                </v-list-item-subtitle>
              </v-list-item-content>
             
            
            </v-list-item>
          </v-list>
        </template>

        <v-card v-if="user">
          <v-list>
            <v-list-item>
              <v-list-item-icon>
                <v-avatar  color="red">
                  <span class="white--text ">{{displayName.substring(0,2).toUpperCase()}}</span>
                </v-avatar>
              </v-list-item-icon>

              <v-list-item-content>
                <v-list-item-title>{{displayName}}</v-list-item-title>
                <v-list-item-subtitle>{{user.role}}</v-list-item-subtitle>
              </v-list-item-content>

              <v-list-item-action>
                
              </v-list-item-action>
            </v-list-item>
          </v-list>

          <v-divider></v-divider>

          <v-list>
            
            <v-list-item>
              <v-list-item-action>
                <v-switch
                  v-model="$vuetify.theme.dark"
                  primary
                ></v-switch>
              </v-list-item-action>
              <v-list-item-title>Dark Theme</v-list-item-title>
            </v-list-item>
<!--
            <v-list-item>
              <v-list-item-action>
                <v-switch v-model="hints" color="purple"></v-switch>
              </v-list-item-action>
              <v-list-item-title>Enable hints</v-list-item-title>
            </v-list-item> -->

          </v-list>

          <v-card-actions>
            <v-spacer></v-spacer>
            
            
             <v-btn color="primary" text @click="logout"><v-icon>mdi-logout</v-icon> Logout</v-btn>
          </v-card-actions>
        </v-card>
      </v-menu>
      
    </v-app-bar>

    <v-content  :class="{
      grey: !$vuetify.theme.dark,
      'lighten-4': !$vuetify.theme.dark
      }">
 
      <v-container fluid >
 
          <router-view></router-view>
      </v-container>
    </v-content>

     
  </v-app>
</template>

<script>
import { mapState, mapActions } from 'vuex'
  export default {
    data: () => ({
      drawers: ['Default (no property)', 'Permanent', 'Temporary'],
      primaryDrawer: {
        model: null,
        type: 'default (no property)',
        clipped: true,
        floating: false,
        mini: false,
      },
    
       fav: true,
      menu: false,
      message: false,
      hints: true,
      drawerList: [
        {"name":"Dashboard","icon":"mdi-view-dashboard","path":"/"},
        {"name":"Deposit","icon":"mdi-bank-transfer-in","path":"/deposit"},
        {"name":"Reports","icon":"mdi-file-chart","path":"/report"},
        {"name":"Customer","icon":"mdi-account-box","path":"/customer"},
        {"name":"Payment Method","icon":"mdi mdi-credit-card-settings-outline","path":"/payment_method"},
        {"name":"Affiliate","icon":"mdi-account-network-outline","path":"/affiliate"},
        {"name":"Users","icon":"mdi-account-multiple","path":"/user"},
        {"name":"Roles","icon":"mdi-key","path":"/role"},
        {"name":"Menus","icon":"mdi-menu","path":"/menu"},
        {"name":"Business","icon":"mdi-domain","path":"/business"},
        {"name":"Department","icon":"mdi-briefcase-account-outline","path":"/department"}]
    }),
    computed: {
      ...mapState({
        user: state => state.auth.user
      }),
      displayName () {
        if (this.user && this.user.displayName) {
           const display = this.user.displayName.trim().length > 0
          return display ? this.user.displayName : this.user.username
        }
        return ""
       
      } 
    },
    methods: {
      ...mapActions({
        logout: 'auth/logout',
        dark: 'auth/dark',
        get: 'api/get'
      })
    },
    watch: {
      '$vuetify.theme.dark' (isDark) {
         this.dark( isDark )
      }
    },
    async created () {
      const { menu } = this.$store.state.auth
      
      if (menu.menu_item && menu.menu_item.length)
        this.drawerList = this.$store.state.auth.menu.menu_item

  
      //get department types
      const departments = await this.get({ collection: 'department' })  
     
      departments.data.docs.forEach(element => {
        this.$store.commit(element.type, element._id)
      });
    
    }
  }

</script>

 