/* eslint no-empty-pattern: ["off"] */
import axios from "axios";
import socket from 'socket.io-client'
import  config from '../config'
const { baseURL } = config 
const io = socket(baseURL)
io.on('connect', () => console.log('socket io connected'))

function stripHtml(html) {
    var tmp = document.createElement("DIV");
    tmp.innerHTML = html;
    return tmp.textContent || tmp.innerText || "";
}

const getInstance = () => {
    const token = localStorage.getItem('token')
    const instance = axios.create({
        baseURL,
        // timeout: 1000,
        headers: { 'Authorization': 'Bearer ' + token }
    });
    return instance
}
const errorHandler = (e, commit) => {
    commit('error', stripHtml((e.response && e.response.data.length) ? e.response.data : e.toString()), { root: true })
}


export default {
    namespaced: true,
    state: {
        io
    },
    mutations: {

    },
    actions: {

        async save({ commit }, payload) {
            const { collection, data } = payload
            if (!collection) {
                return commit('error', 'missing collection name in action "api/save"', { root: true })
            }
            if (!data) {
                return commit('error', 'missing data in action "api/save"', { root: true })

            }
            const instance = getInstance()
            return await instance.post(collection, data).catch(e => errorHandler(e, commit))

        },
        async update({ commit }, payload) {
            const { collection, data, id } = payload

            if (!collection) {
                return commit('error', 'missing collection name in action "api/update"', { root: true })
            }
            if (!data) {
                return commit('error', 'missing data in action "api/update"', { root: true })

            }
            if (!id) {
                return commit('error', 'missing id in action "api/update"', { root: true })

            }
            const instance = getInstance()
            return await instance.put(collection + '/' + id, data).catch(e => errorHandler(e, commit))

        },
        async remove({ commit }, payload) {
            const { collection, id } = payload
            if (!collection) {
                return commit('error', 'missing collection name in action "api/save"', { root: true })
            }
            if (!id) {
                return commit('error', 'missing id in action "api/remove"', { root: true })

            }
        

            const instance = getInstance()
            return await instance.delete(collection + '/' + id).catch(e => errorHandler(e, commit))

        },
        async getById({ commit }, payload) {
            const { collection, id } = payload
            if (!collection) {
                return commit('error', 'missing collection name in action "api/getById"', { root: true })
            }
            if (!id) {
                return commit('error', 'missing id in action "api/getById"', { root: true })
            }
            const instance = getInstance()
            return await instance.get(collection + '/' + id).catch(e => errorHandler(e, commit))
        },

        async get({ commit }, payload) {
            let { collection, query, search } = payload
            query = query || {}
            if (search)
                query.search = search
            if (!collection) {
                return commit('error', 'missing collection name in action "api/get"', { root: true })
            }

            const instance = getInstance()
            return await instance.get(collection, {
                params: query
            }).catch(e => errorHandler(e, commit))
        },
    }
}