import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import store from './../store'

Vue.use(VueRouter)





  const routes = [
  {
    path: '/login',
    name: 'Login',
    
    component: () => import('../views/Login.vue')
  },
  {
    path: '/screen',
    name: 'Screen',

    component: () => import('../views/Screen.vue')
  },
  {
    path: '/store',
    name: 'Store',

    component: () => import('../views/Store.vue')
  },
  {
    path: '/',
    component: Home,
    meta: {
      requierAuth: true
    },
    children: [
      {
        path: '/',
        name: 'Dashboard',
        component: () => import('../views/Dashboard.vue'),
        meta: {
          requierAuth: true
        },
      },
      {
        path: '/404',
        name: '404',
        component: () => import('../views/404.vue'),
        meta: {
          requierAuth: true
        },
      },
      {
        path: '/retention',
        name: 'Retention',
        component: () => import('../views/Retention'),
        meta: {
          requierAuth: true
        },
      },
      {
        path: '/ftd',
        name: 'FTD',
        component: () => import('../views/FTD'),
        meta: {
          requierAuth: true
        },
      },
      {
        path: '/report',
        name: 'Report',
        component: () => import('../views/Report.vue'),
        meta: {
          requierAuth: true
        },
      },
      {
        path: '/report2',
        name: 'Report2',
        component: () => import('../views/ReportNew.vue'),
        meta: {
          requierAuth: true
        },
      },
      {
        path: '/:collection',
        name: 'Items',
        component: () => import('../views/Items.vue'),
        meta: {
          requierAuth: true
        },
      },
      {
        path: '/:collection/new',
        name: 'Item',
        component: () => import('../views/Item.vue'),
        meta: {
          requierAuth: true
        },
      },
      {
        path: '/:collection/:id',
        name: 'ItemUpdate',
        component: () => import('../views/Item.vue'),
        meta: {
          requierAuth: true
        },
      },
      {
        path: '/view/:collection/:id',
        name: 'ViewItem',
        component: () => import('../views/View.vue'),
        meta: {
          requierAuth: true
        },
      }
    ]
  }
 
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})


// const adminRole = ['admin', 'manager']
router.beforeEach((to, from, next) => {
  const { requierAuth } = to.meta
  if(requierAuth && !store.getters['auth/isAuthenticated'])
    return next('/login')
  if(to.name == 'Login' && store.getters['auth/isAuthenticated'])
    return next('/')
 
    // access controle
    if (to.params.collection) {
      const { rights } = store.state.auth.user
      if (!rights || (rights && !rights.length))
        return next()
      else {
        let permissions = rights.filter(el => el.collection == to.params.collection)
        if (!permissions.length)
          return next()
        else 
          if (permissions[0].read.value)
            return next()
          else
            return next('/404') 

        
      }
    }
 


  return next()
})
export default router
