<template>
  <v-app>
    <router-view ></router-view>
    <v-snackbar
      v-model="snackbar.value"
      :absolute="snackbar.absolute"
      :color="snackbar.color"
      :bottom="snackbar.y === 'bottom'"
      :left="snackbar.x === 'left'"
      :multi-line="snackbar.mode === 'multi-line'"
      :right="snackbar.x === 'right'"
      :timeout="snackbar.timeout"
      :top="snackbar.y === 'top'"
      :vertical="snackbar.mode === 'vertical'"
    >
    {{ snackbar.text }}  
      <v-btn
        dark
        icon
        @click="snackbarClose"
      >
        <v-icon>mdi-close</v-icon>
      </v-btn>
    </v-snackbar>
  </v-app>
  
</template>

<script>
  import { mapActions, mapState, mapGetters } from 'vuex'
 
 
export default {
  name: 'App',
  computed: {
    ...mapState({ 
      user: state => state.auth.user,
      snackbar: state => state.snackbar, 
      error: state => state.error,
      success: state => state.success,
      io: state => state.api.io,

    }),
    ...mapGetters({
      isAuthenticated: 'auth/isAuthenticated'
    })
  },
  methods: {
    ...mapActions( {
      snackbarOpen: 'snackbar/open', 
      snackbarClose: 'snackbar/close',
    })
  },
  watch: {
   isAuthenticated (v) {
    
     if (v) {
       this.$router.push('/')
       this.io.emit('setUser', this.user._id)
     } else {
       this.$router.push('/login')
     }
  
     
   },
   error (msg) {
     if (msg.length) {
       this.snackbarOpen([msg, 'red']).then(() => {
         setTimeout(() => {
           this.$store.commit('error', '')
         }, this.snackbar.timeout)
       })
       
     }
   },
   success (msg) {
     if (msg.length) {
       this.snackbarOpen([msg, 'green']).then(() => {
         setTimeout(() => {
           this.$store.commit('success', '')
         }, this.snackbar.timeout)
       })
       
     }
   }
  }, 
  created () {
     

    if (this.user) {
      this.$vuetify.theme.dark = this.user.dark_theme
      this.io.emit('setUser', this.user._id)
    }
  }
};
</script>
