import Vue from 'vue'
import Vuex from 'vuex'

// moudles
import auth from "./auth";
import snackbar from "./snackbar";
import api from './api'
Vue.use(Vuex)

export default new Vuex.Store({
  state:{
    error: '',
    success: '',
    //departments
    ftd: null,
    rtn: null
  },
  mutations: {
    error (state, message) {
      state.error = message
    },
    success (state, message) {
      state.success = message
    },
    ftd(state, department) {
      state.ftd = department
    },
    rtn(state, department) {
      state.rtn = department
    },
  },
  modules: {
    snackbar,
    auth,
    api
  }
})
